import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/IndexView.vue'
import ProductCenter from '../views/ProductCenterView.vue'
import CompanyIntroduction from '../views/CompanyIntroductionView.vue'
import ContactUs from '../views/ContactUsView.vue'
import Homepage from '../views/HomepageView.vue'


import MHome from '../mViews/MHome'
import MIndex from '../mViews/MIndex'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    redirect: '/index',
    component: HomeView,
    children: [
      {
        path: '/index',
        name: 'Homepage',
        component: Homepage,
        props: route => ({ columnId: route.query.columnId })
      },
      {
        path: "/productCenter",
        name: "productCenter",
        component: ProductCenter,
        meta: {
          requiresAuth: true
        }
      },
      {
        path: "/companyIntroduction",
        name: 'companyIntroduction',
        component: CompanyIntroduction,
        meta: {
          requiresAuth: true
        }
      },
      {
        path: '/contactUs',
        name: 'contactUs',
        component: ContactUs
      },
    ]
  },
  {
    path: '/m',
    name: 'mIndex',
    redirect: "/m/home",
    component: MIndex,
    children: [
      {
        path: "home",
        name: 'mHome',
        component: MHome,
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'hash',
  // base: process.env.BASE_URL,
  routes
})

export default router
